
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
name: 'FileLogin',
components: {
XHeader
},
data () {
return {
code:'',
idcard:'',
disable: true,
isSend:false,
countdown: '',
}

},

mounted(){
this.sendCode();
},
methods: {
sendCode(){
this.$ajax.post('',{
'MSH.1':'healthFileAssociateService',
'MSH.2':'sendSMS',
'token':this.$root.loginInfo.token,
}).then(data => {
if(typeof data !='undefined'){
if(data.status==1){
this.isSend = true;
this.timer();
}else{
this.$vux.toast.text("发送失败", "bottom");
}
}
}).catch(error => {
console.log(error);
});

},


timer(){
this.countdown=60;
var timer = setInterval(()=>{
this.countdown --;
if(this.countdown == 0){
clearInterval(timer);
this.countdown = '重新发送';
}
},1000);



},
NextStep(){
if(!this.disable){
this.$ajax.post('',{
'MSH.1':'healthFileAssociateService',
'MSH.2':'vidCodeAndCard',
'token':this.$root.loginInfo.token,
'card':this.idcard,
'telcode':this.code,
'token':this.$root.loginInfo.token,
}).then(data => {
if(typeof data !='undefined'){
if(data.cardStatus==1&&data.codeStatus==1){
this.$router.push({name: "setPWD", params: {type: '2'}});
}else{
if(data.cardStatus==2){
this.$vux.toast.text("身份证号码错误", "bottom");
}
if(data.codeStatus==2){
this.$vux.toast.text("验证码错误", "bottom");
}

}
}
}).catch(error => {
console.log(error);
});
}
}
},
watch: {
idcard(val){
if(val.length == 18){
this.disable = false;
return;
}
this.disable = true;
}
}
}



